@import url(https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@600&family=Nunito:wght@200;300;400;500&display=swap);
body{
  width:100vw;
  margin:0  !important;
  min-height: 100vh;
  padding: 0 !important;
  overflow-x: hidden;
  font-family: 'Nunito', sans-serif;
}
::-webkit-scrollbar{
  width:7px;
  background: rgb(253, 253, 253);
}
::-webkit-scrollbar-thumb{
  width:7px;
  background:#717CE3;
}
.home{
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    align-items: center;
    height:90vh;
    margin:0;
    justify-content: center;
}
.home-content{
    display:flex;
    flex-direction: row;
    flex-grow: 1;
    align-items: center;
    width:100vw;
    justify-content: space-between;
}
.home-center{
    display:flex;
    flex-direction: column;
    flex-grow: 1;
    margin-left:3vw;
    align-items: center;
    padding-left: 6vh;
    justify-content: center;
}
.home h1{
    font-weight: 200;
    font-size:65px;
    text-align: center;
    margin:0;
    line-height: 110%;
}
.icons{
    display:flex;
    flex-direction:column;
    padding-right: 2vh;
    margin-right:3vw;
}
.icons img{
    height:4vh;
    width:4vh;
    margin:2vh 0;
}
#purple{
    color:#717CE3;
}
#subtitle{
    color:#888888;
    text-align: center;
    font-size:28px;
}
.buttons{
    display:flex;
    flex-direction:row;
    align-items:center;
}
button{
    padding:1.6vh 4vh;
    margin:2vh 2vh 8vh 2vh;
    border:none;
    cursor:pointer;
    outline:none;
}
.button-link{
    text-decoration: none;
    color:white;
}
#cta{

    background:black;
    text-decoration: none;

    color:white;
    font-family: 'Nunito', sans-serif;
    font-weight:300;
    letter-spacing: 0.1rem;
    border:solid 2px #222222;
    font-size:14px;
}
#cta2{
    color:#222222;
    background:white;
    font-family: 'Nunito', sans-serif;
    font-weight:700;
    letter-spacing: 0.1rem;
    font-size:14px;
    border:solid 2px #222222;
}
#down{
    display: flex;
    align-items:center;
    justify-content: center;
    height:50px;
    width:50px;
    border-radius:30px;

    cursor:pointer;
    background:#000000;

    opacity: 0.8;
    margin-bottom: 4vh;
}
#down img{
    height:25px;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}
@media screen and (max-width:1100px){
    .home h1{
        font-size:55px;
    }
    #subtitle{
        font-size:20px;
        margin:2vh 4vh;
    }
    .icons img{
        height:3vh;
        margin:1vh 0;
    }
}
@media screen and (max-width:800px){
    .home-content{
        display:flex;
        flex-direction: column;
        flex-grow: 1;
        align-items: center;
        width:100vw;
        justify-content: space-between;
    }
    .home-center{
        display:flex;
        flex-direction: column;
        flex-grow: 1;
        margin-left:0;
        padding-left: 0;
        margin-top:8vh;
        align-items: center;
        justify-content: center;
    }
    .icons{
        display:flex;
        flex-direction:row;
        margin-right:0;
        margin-bottom: 4vh;
    }
    .icons img{
        height:25px;
        width:auto;
        margin:0 2vh;
    }
}
@media screen and (max-width:500px){
    .home h1{
        font-size:45px;
    }
    #subtitle{
        font-size:18px;
        margin:2vh 4vh;
    }
}
.nav{
    display:flex;
    flex-direction:row;
    max-height:10vh;
    height:10vh;
    width:94vw;
    position:-webkit-sticky;
    position:sticky;
    z-index:100;
    top:0;
    background:rgb(255, 255, 255);
    padding:0 3vw;
    align-items: center;
    justify-content: space-between;
}
.inner-nav{
    display:flex;
    flex-direction: row;
    text-decoration: none;
    color:black;
    font-size:16px;
}
.navlink{
    color:black;
    text-decoration: none;
}
.inner-nav img{
    height:50px;
    margin:0;
    margin-right: 2vh;
    border-radius: 30px;
}
.inner-nav p{
    cursor:pointer;
    color:black;
    font-size:15px;

}
.inner-nav-item{
    margin:auto 2vh;
    font-weight: 500;
    font-size:12px;
    letter-spacing: 0.1rem;
}

#hide-nav{
    font-weight: 500;
    font-size:16px;
    cursor: pointer;
    text-decoration: none;
    color:black;
}
@media screen and (max-width:800px) {
    #hide-nav{
        display:none;
    }
}
.inner-nav a{
    color:black;
}
.about{
    background-color:#fafafd;
    padding:20vh 3vw;
    display:flex;
    background-size: 60%;
    flex-direction:column;
    align-items:flex-start;
    justify-content: center;
}
.about h1{
    font-weight:300;
    color: #717CE3;
}
.about h3{
    font-weight:400;
    color: #717CE3;
}
.about p{
    color:grey;
    width:60%;
    font-size:20px;
}
#about-tools{
    margin-bottom:5vh;
}
.contact{
    background:black;
    height:100vh;
    padding:0 3vw;
    display:flex;
    color:grey;
    flex-direction: column;
    justify-content: flex-end;
}
.contact h1{
    color: #717CE3;
    font-size:55px;
    margin:3vh 0;
    font-weight:300;
}
.contact p{
    font-size:22px;
    margin:1vh 0;
}
.contact img{
    height:auto;
    width:60vh;
    position: absolute;
    align-self: flex-end;
}
.contact a{
    padding:2vh;
    color:rgb(201, 201, 201);
    border:solid 2px rgb(201, 201, 201);
    text-decoration: none;
    margin:2vh 0;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
#footer{
    margin:3vh 0;
    font-size:17px;
    margin-bottom: 20vh;
}
.work{
    min-height:100vh;
}
.nav-work{
    margin-top:6vh;
    display:flex;
    align-items:center;
    justify-content:center;
    padding:4vh;
}
.nav-work p{
    margin:0 2vh;
    font-weight:400;
    font-size:18px;
    cursor:pointer;
    border-bottom: solid 2px white;
}
.nav-selected{
    color: #717CE3;
    font-weight:500 !important;
    border-bottom: solid 2px #717CE3 !important;
}
.work-grid{
    display:flex;
    flex-direction:row;
    flex-wrap: wrap;
    align-items:center;
    justify-content:center;
    padding:4vh 5vw;
}
.card{
    margin:4vh;
    height:49vh !important;
    width:39vh;
    color:white;
    cursor:pointer;
    transition: 0.6s;
}
.card-inner{
    height:45vh;
    width:35vh;
    display:flex;
    padding:2vh;
    flex-direction: column;
    justify-content: flex-end;
    transition: 0.6s;
    background: linear-gradient(-180deg, rgba(0,0,0,0.02874653279280459) 0%, rgba(0,0,0,0.88) 65%, rgba(0,0,0,1) 100%);
}
.card:hover{
    height:55vh !important;
    transition: 0.6s;
}
.card-inner:hover{
    height:51vh !important;
    transition: 0.6s;
}

.card h2{
    font-weight:300;
    margin:0;
}
.card p{
    font-weight:300;
    color:grey;
    margin:1vh 0;
    font-size:15px;
}
.card a{
    text-decoration: none;
    padding:1vh 2vh;
    margin:1vh 0;
    border:solid 1.5px white;
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    color: white;
    background: rgba(255, 255, 255, 0.116);
}
.img-card{
    margin:4vh; 
    width:38vh;
    height: 38vh;
    transition: 0.4s;
    cursor: pointer;
}
.img-card-in{
    width:38vh;
    height: 38vh;
    transition: 0.4s;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    background: linear-gradient(-180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.527) 85%, rgba(0,0,0,1) 100%);
}
.img-card-in p{
    padding:2vh;
    color:white;
}
.img-card:hover{
    height:42vh !important;
    width:42vh;
    transition: 0.4s;
}
.img-card-in:hover{
    height:42vh !important;
    width:42vh;
    transition: 0.4s;
}
a{
    text-decoration: none;
}
.illustration-card{
    margin:4vh; 
    width:38vh;
    height: 38vh;
    transition: 0.4s;
    cursor: pointer;
}

.illustration-card:hover{
    height:42vh !important;
    width:42vh;
    transition: 0.4s;
}

